import React, { Component } from "react";
import classes from "./css/main.module.css";

import Player from "./songPlayer";
import Draggable from "react-draggable";

import oasis from "./css/table.module.css";

class Info extends Component {
  state = {
    toggle: true,
    api_res: "",
    musicThere: false,
    picturePreview: false,
    projectThere: false,
  };
  constructor(props) {
    super(props);
    this.state = { apiResponse: "" };
  }
  // call_backend() {
  //   fetch("http://localhost:9000/getDb")
  //     .then((res) => res.json())
  //     .then((res) => this.setState({ api_res: res.title }))
  //     .catch((err) => err);
  // }
  // componentWillMount() {
  //   this.call_backend();
  // }
  render() {
    const isMusicPlay = this.state.musicThere;
    const projectThere = this.state.projectThere;
    const isPicture = this.state.picturePreview;
    return (
      <div className={classes.info}>
        <div className={classes.wrapper}>
          <div>
            <div className={oasis.mainList}>
              <button
                className={oasis.draw_border}
                onClick={() => {
                  this.setState({
                    projectThere: true,
                  });
                }}
                disabled
              >
                Projects(Don't Press)
              </button>

              <button
                className={oasis.draw_border}
                onClick={() => {
                  this.setState({
                    musicThere: true,
                  });
                }}
              >
                Wierd Music I Wrote
              </button>
            </div>
          </div>
        </div>
        <div className={oasis.musicPop}>
          {isMusicPlay ? (
            <Draggable>
              <div>
                <button
                  className={oasis.closeButton}
                  onClick={() => {
                    this.setState({ musicThere: false });
                  }}
                >
                  x
                </button>
                <Player />
              </div>
            </Draggable>
          ) : (
            <span></span>
          )}
        </div>

        <div className={classes.picture}>
          <h1 className={classes.title}>
            <ul className={classes.list}>
              <li>Hi There! I'm Sagar</li>
              <li> म सागर।</li>
              <li>さがら です</li>
            </ul>
          </h1>
          <img
            height="40%"
            className={classes.pictureI}
            alt="me"
            src="me.svg"
          />
        </div>
        <p className={classes.para}>
          I am a "software engineer".I am currently working on some stuff. I
          will eventually add some more thing to this website ,showing casing
          the things I've worked on.
        </p>

        <div className={classes.infoW}>
          Me around the internet
          <div>
            <a
              href="https://www.linkedin.com/in/sagr2848/"
              target="_blank"
              rel="noreferrer"
            >
              <img
                src="https://img.icons8.com/doodle/50/000000/linkedin.png"
                alt="li"
              />
            </a>
            <a
              href="https://github.com/sgr2848"
              target="_blank"
              rel="noreferrer"
            >
              <img
                src="https://img.icons8.com/doodle/48/000000/github.png"
                alt="git"
              />
            </a>
            <a
              href="https://www.instagram.com/_sagr__/"
              target="_blank"
              rel="noreferrer"
            >
              <img
                src="https://img.icons8.com/doodle/48/000000/instagram-new.png"
                alt="insta"
              />
            </a>
            <a
              href="https://soundcloud.com/grtsha"
              target="_blank"
              rel="noreferrer"
            >
              <img
                src="https://img.icons8.com/doodle/48/000000/soundcloud--v1.png"
                alt="shound"
              />
            </a>
            <a
              href="https://drive.google.com/file/d/16UiIuMhtTmlxdF8wBu4-GJ4_7zN0PuZ6/view?usp=sharing"
              target="_blank"
              rel="noreferrer"
            >
              <img
                src="https://img.icons8.com/offices/40/000000/open-resume.png"
                alt="res"
              />
            </a>
          </div>
        </div>
      </div>
    );
  }
  player() {
    return (
      <div>
        <Player />
      </div>
    );
  }
}

export default Info;
