import React, { Component } from "react";
import Carousel, { autoplayPlugin } from '@brainhubeu/react-carousel';
import '@brainhubeu/react-carousel/lib/style.css';
import css_mod from "./css/project.module.css";


class Projects extends Component {
    render() {
        return (<div className={css_mod.mainDiv}>
            <div className={css_mod.insideDiv}>
                <div className={css_mod.projTile}>
                    <h1>Student First</h1>
                    <Carousel
                        plugins={[
                            'infinite',
                            {
                                resolve: autoplayPlugin,
                                options: {
                                    interval: 2000,
                                }
                            },
                        ]}
                        animationSpeed={1000}
                    >
                        <img src="/project/s_i.jpg" alt="s-i" className={css_mod.imgI} width="480" height="260" />
                        <img src="/project/s_ii.jpeg" alt="s-ii" className={css_mod.imgI} width="480" height="260" />
                        <img src="/project/s_iii.jpg" alt="s-iii" className={css_mod.imgI} width="480" height="260" />
                        <img src="/project/s_iv.jpg" alt="s-iv" className={css_mod.imgI} width="480" height="260" />
                        <img src="/project/s_v.jpg" alt="s-v" className={css_mod.imgI} width="480" height="260" />
                    </Carousel>


                    <a href="github.com/AswinTorch/TTP-Capstone-Project-Server">Frontend</a>  | <a href="github.com/AswinTorch/TTP-Capstone-Project-Client">Backend</a> |
                    <a href="https://ttp-capstone-project.web.app/">Site</a>
                    <div><i>Tech Used: React, Redux, TypeScript,Firebase,Python</i>
                        <p>My three teammates and I  put together a student dashboard web application. The frontend of the application is created using React  and Redux. The backend depends on ExpressJS and the FirebaseDB is sed as database . The user authentication uses Firebase Auth. The data used in the professor are the real data that are scrapped from CUNY's website using selenium and beautiful soup. The app allows you to add, drop or swap you class view your financials and search for class and also see the professor's reviews. The backend also implements a cache in order to lessen the request sent to the firebase db, as it only allows 10k read a day.</p></div>
                </div>
                <div className={css_mod.projTile}>

                    <h1>Job Posting Discord Bot</h1>
                    <Carousel
                        plugins={[
                            'infinite',
                            {
                                resolve: autoplayPlugin,
                                options: {
                                    interval: 2000,
                                }
                            },
                        ]}
                        animationSpeed={1000}
                    >
                        <img src="/project/csd_i.jpg" alt="cs-i" className={css_mod.imgI} width="480" height="260" />
                    </Carousel>
                    <a href="https://github.com/Career-Spotter/Career-Spotter-bot">Bot Repo</a> | <a href="https://github.com/Career-Spotter/b_good">Backend</a>
                    <div><i>Tech Used: NodeJS, Python, Selenium, Kafka, PostGreSQL, Go Lang</i><p>The following bot is written in JavaScript which updates the discord server with job posting as shown above, The jobs are scrapped using python from various job posting site like glassdoor, indeed etc and stored in postgres for future use(other possible future projects). The backend is written in go lang and fetches job posted less than 10 days ago. The no jobs are found it triggers the python script to fetch scrape more data. Kafka is used as message broker for all different server to talk to each other.</p>
                    </div>
                </div>
                <div className={css_mod.projTile}>

                    <h1>No Lang</h1>
                    <Carousel
                        plugins={[
                            'infinite',
                            {
                                resolve: autoplayPlugin,
                                options: {
                                    interval: 2000,
                                }
                            },
                        ]}
                        animationSpeed={1000}
                    >
                        <img src="/project/nl_i.jpg" alt="s-id" className={css_mod.imgI} width="480" height="260" />
                        <img src="/project/nl_ii.jpg" alt="s-ids" className={css_mod.imgI} width="480" height="260" />
                        <img src="/project/nl_iii.jpg" alt="s-ida" className={css_mod.imgI} width="480" height="260" />
                    </Carousel>
                    <a href="https://github.com/sgr2848/nolang">Github</a>
                    <div><i>Tech Used: Rust</i><p>Built a interpreted language using rust. The complexity with this project was that it was not a simple task to write a graph structure in Rust like in C/C++ by using pointers, the whole borrow checker feature in Rust would be of no use if we were to go that route. So to make a dynamically modifiable tree I implemented a custom data structure using vector and a index based node to reconstitute the vector on every operations.</p></div>
                </div>
                <div className={css_mod.projTile}>

                    <h1>Request Profiler </h1>
                    <Carousel
                        plugins={[
                            'infinite',
                            {
                                resolve: autoplayPlugin,
                                options: {
                                    interval: 2000,
                                }
                            },
                        ]}
                        animationSpeed={1000}
                    >
                        <img src="/project/hp_i.jpg" alt="s-idss" className={css_mod.imgI} width="480" height="260" />
                        <img src="/project/hp_ii.jpg" alt="s-idsa" className={css_mod.imgI} width="480" height="260" />
                    </Carousel>
                    <a href="https://github.com/sgr2848/rust_http_profiler">Github</a>
                    <div><i>Tech Used: Rust</i><p>The following Command Line Application profiles the server request time for any given url for certain given number. It uses plain sockets to send the HTTPS request </p></div>
                </div>
                <div className={css_mod.projTile}>

                    <h1>Personal Website </h1>
                    <Carousel
                        plugins={[
                            'infinite',
                            {
                                resolve: autoplayPlugin,
                                options: {
                                    interval: 2000,
                                }
                            },
                        ]}
                        animationSpeed={1000}
                    >
                        <img src="/project/st_i.gif" alt="s-idasf" className={css_mod.imgI} width="480" height="260" />

                    </Carousel>
                    <a href="https://github.com/sgr2848/rust_http_profiler">Github</a>
                    <div><i>Tech Used: NodeJS, React, ExpressJS</i><p>A simple single paged web site.</p></div>
                </div>
                <div className={css_mod.projTile}>

                    <h1>Bank Dashboard </h1>
                    <Carousel
                        plugins={[
                            'infinite',
                            {
                                resolve: autoplayPlugin,
                                options: {
                                    interval: 2000,
                                }
                            },
                        ]}
                        animationSpeed={1000}
                    >
                        <img src="/project/i.gif" alt="s-idaf" className={css_mod.imgI} width="480" height="260" />
                        <img src="/project/ii.gif" alt="s-idsaq" className={css_mod.imgI} width="480" height="260" />
                        <img src="/project/iii.gif" alt="sq-id" className={css_mod.imgI} width="480" height="260" />
                        <img src="/project/iv.gif" alt="rwrs-id" className={css_mod.imgI} width="480" height="260" />

                    </Carousel>
                    <a href="https://github.com/sgr2848/rust_http_profiler">Github</a>
                    <div><i>Tech Used: Java, SwingFX, mySQL</i><p>A very simple GUI I made a long time ago in java.</p></div>
                </div>
            </div>
        </div>);
    }
}
export default Projects;